const DEFAULT_IMG_URL = 'http://www.sucaijishi.com/uploadfile/2018/0508/20180508023717621.png'
export default {
  data () {
    return {
      userInfo: {},
      defaultImgUrl: DEFAULT_IMG_URL,
      menuList: [
        {
          title: '待付款',
          fontCls: 'icon-pre-pay',
          index: 1
        },
        {
          title: '待收货',
          fontCls: 'icon-pre-send',
          index: 2
        },
        {
          title: '退款/退货',
          fontCls: 'icon-goods-reject',
          index: 3
        },
        {
          title: '全部订单',
          fontCls: 'icon-all-order',
          index: 0
        }
      ]
    }
  },
  onShow () {
    this.userInfo = wx.getStorageSync('userInfo')
  },
  methods: {
    toLogin () {
      wx.navigateTo({ url: '/pages/login/main' })
    },
    makeCall () {
      wx.makePhoneCall({ phoneNumber: '17605089381' })
    },
    toOrder (index) {
      wx.navigateTo({ url: '/pages/order/main?index=' + index })
    }
  }
}
